import h from './LeftBar.module.css'
import Name from './Name'
import Line from '../Line/Line'
import Following from './Following/Following'
const LeftBar = (props) =>{
  
  let FollowingData = [
    {following:'following', id:1, count:'34'},
  ]
  let newFollowing = FollowingData.map(item => <Following follow={item.following} count={item.count} />);
  
  let FData = [
    {following:'followers', id:1, count:'1534'},
  ]
  let newF = FData.map(item => <Following follow={item.following} count={item.count} />);
    return(
        <div className={h.LeftBar}>
          <div className={h.gr}>
          <img src='data:image/jpeg;base64,' />
          <div>
             <Name /> 
             <p className={h.text_under_name}>Graphic Designer at Self Employed</p>
             <Line />
             {/* <Following follow={FollowingData[0].following} count={FollowingData[0].count} /> */}
              {newFollowing}
             <Line />
             
             {newF}
             {/* <Following follow={FollowingData[1].Followers}  count={FollowingData[1].count}/> */}
             <Line />
             <div className={h.view_profile}><a href="#">View Profile</a></div>
          </div>
        </div>
        </div>
    );
}

export default LeftBar;